<template>
  <input :value="modelValue" @input="updateInput" class="input" type="text" />
</template>

<script>
export default {
  name: 'my-input',
  props: {
    modelValue: [String, Number],
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value);
    },
  },
};
</script>

<style>
.input {
  width: 100%;
  margin-top: 10px;
  border: 1px solid teal;
  padding: 10px 15px;
  border-radius: 5px;
  background: #eee;
}
.search {
  background: #fff;
}
</style>
